import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import SignUpP from '../../screens/auth/SignUpP';
import {ossoApi} from '../../redux/api'
import {Register} from '../../redux/auth/action/index'
import {connect} from 'react-redux'
import { Redirect } from 'react-router-dom'
import {
    isValidPhoneNumber,
    isValidEmail,
    validString,
} from '../../Utils/Validation';
function SignUp(props) {
    const [email, setEmail] = useState(null);
    const [name,setName] =useState(null)
    const [phone,setPhone]=useState(null)
    const [password, setPassword] = useState(null);
    const [cnfPassword,setCnfPassword]=useState(null);
    const [isRegister,setIsRegister]=useState(false)
    const handleClick = () => {
        if (!isValidEmail(email))
        {
            alert('Enter valid email')
            return
        }
        if (!validString(name)) {
            alert('Enter name')
            return
        }
        if (!isValidPhoneNumber(phone)) {
            alert('Enter phone number')
            return
        }
        if (!(password)) {
            alert('Enter password')
            return
        }
        if (!(cnfPassword)) {
            alert('Enter confirm password')
            return
        }
        if(password==cnfPassword)
        {
            let data={
                email,
                password,
                phone,
                fullname:name
            }
            let dataInput=JSON.stringify(data)
            ossoApi.post(`auth/register`, dataInput, {
                headers: {
                    'content-type': 'application/json'
                }
            }).then(res=>{
                console.log(res)
                if(res.data && res.data.token && res.data.data)
                {
                    props.Register(res.data.data,res.data.token)
                    alert('Registered Sucessfully')
                    setIsRegister(true)
                }
                else{
                    alert('Enter Valid')
                }

            }).catch(err=>{
                console.log(err)
                alert('Try Again!')
            })
        }
        else
        {
            alert('Password mismatched')
        }
    }
    if(isRegister)
    {
        props.history.push('/');
    }
    return (
        <SignUpP
            setEmail={setEmail}
            setName={setName}
            setPhone={setPhone}
            setPassword={setPassword}
            setCnfPassword={setCnfPassword}
            handleClick={handleClick}
        />
    );
}

export default connect(null,{Register})(SignUp);
