import { ossoApi} from '../../api'
import { 
    USER_LOGIN,
    USER_REGISTER,
} from '../constants';
import { connect } from 'react-redux';

export const Login=(user,token)=>{
    return {
        type:USER_LOGIN,
        payload:{user,token}
    }
}
export const Register=(user,token)=>{
    return{
        type:USER_REGISTER,
        payload:{user,token}
    }
}
export const LogOut = () => ({ type: 'RESET', payload: null });